import React from 'react';
import {Button, Label, Select} from "flowbite-react";

const Welcome = ({setQuizCount, setTotalTime, startGame}) => {
    return (
            <div className={["quiz_timer"]}>
                <div className={"quiz_select"}>
                    <h1>    </h1>
                    <Select
                        id="quiz"
                        required
                        onChange={(event) => setQuizCount(event.target.value)}
                    >
                        <option value={5}>
                            5
                        </option>
                        <option value={10}>
                            10
                        </option>
                        <option value={15}>
                            15
                        </option>
                        <option value={20}>
                            20
                        </option>
                    </Select>
                </div>
                <Button className="btn_start" onClick={() => startGame()}></Button>
                <div className={"timer_select"}>
                    <h1>  .</h1>
                    <Select
                        id="timer"
                        required
                        onChange={(event) => setTotalTime(event.target.value)}
                    >
                        <option value={60}>
                            60
                        </option>
                        <option value={100}>
                            100
                        </option>
                        <option value={150}>
                            150
                        </option>
                        <option value={200}>
                            200
                        </option>
                    </Select>
                </div>
            </div>
    );
};

export default Welcome;