// Import Swiper React components
import {Swiper, SwiperSlide} from 'swiper/react';
import {useState, useRef, useEffect} from 'react';
import {Button, ListGroup, Progress, Select, Label} from 'flowbite-react';

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import './App.css';

// import required modules
import Question from "./components/Question";
import Welcome from "./components/Welcome";
import db from "./db";
import CompleteModal from "./components/CompleteModal";

function App() {
    const { itemDB } = db;
    // ,   
    const [quizCount, setQuizCount] = useState(5); //   5
    const [totalTime, setTotalTime] = useState(60); //   60

    //
    const [count, setCount] = useState(0);

    //random     slice 
    const [items, setItems] = useState(itemDB);

    //   0-100     
    const [timer, setTimer] = useState(0);

    //    
    const [start, setStart] = useState(false);

    const [show, setShow] = useState(false);

    //time progress bar      .
    let timePiece = Math.round(100 / totalTime * 1000) / 1000;





    //    .
    function reset() {
        setCount(0);
        setTimer(0);
        setQuizCount(5);
        setTotalTime(60);
        setStart(false);
    }

    // .
    //1. DB        slice(copy) item .
    //2.    true .
    //3. 0-100     progressbar .
    function startGame() {
        setItems(itemDB.sort(() => Math.random() - 0.5).slice(0,quizCount))
        setStart(true);
    }

    useInterval(() => {
        if (start && timer < 100) {
            console.log(timePiece);
            setTimer(timer => timer + timePiece * 0.05); //
        } else {
            setShow(true);
        }
    }, 1000 * 0.05);//

    function useInterval(callback, delay) {
        const savedCallback = useRef();

        // Remember the latest callback.
        useEffect(() => {
            savedCallback.current = callback;
        }, [callback]);

        // Set up the interval.
        useEffect(() => {
            function tick() {
                savedCallback.current();
            }
            if (delay !== null) {
                let id = setInterval(tick, delay);
                return () => clearInterval(id);
            }
        }, [delay]);
    }

    return start ? (
        <>
            <Progress progress={timer} size={"xl"}/>
            <Question
                setCount={setCount}
                items={items}
            ></Question>
            <p> : {count}</p>
            <Button onClick={() => reset()}></Button>
            <CompleteModal
                onClose={() => {}}
                onClick={() => {}}
                show={show}
            >
                    </CompleteModal>
        </>
    ) : <Welcome
            setQuizCount={setQuizCount}
            setTotalTime={setTotalTime}
            startGame={startGame}
        >
        </Welcome>

        ;
}

export default App;
